.main-nav__wrapper {
  position: fixed;
  width: 100%;
  min-height: 68px;

  background-color: rgba($color-black, 0.3);
  z-index: 2;
}

.main-nav__wrapper--nojs {
  position: relative;
}

.main-nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;

  @media (min-width: $desktop-width) {
    max-width: 960px;
    margin: 0 auto;
    flex-wrap: nowrap;
  }
}

.main-nav__inner {
  display: flex;
  justify-content: space-between;
  width: 100%;

  padding: 18px 20px;
  align-items: center;

  @media (min-width: $tablet-width) {
    padding: 35px 20px;
  }

  @media (min-width: $desktop-width) {
    width: auto;
    padding-left: 0;
    padding-right: 0;
  }
}

.main-nav__list {
  @include reset-list;
  text-align: center;
  width: 100%;

  @media (min-width: $desktop-width) {
    display: flex;
    flex-wrap: wrap;
    width: auto;
  }
}

.main-nav--opened {
  @media (max-width: $max-tablet-width) {
    background-color: $color-dark-grey;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--opened .site-list__item {
  display: block;
}

.main-nav--closed .site-list__item {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
  }
}

.main-nav__toggle {
  @include reset-btn;

  display: block;
  position: relative;
  width: 40px;
  height: 25px;

  cursor: pointer;

  &:hover {

    &.main-nav__toggle span,
    &::before,
    &::after {
      background-color: $color-pink;
    }
  }

  &:active {

    &.main-nav__toggle span,
    &::before,
    &::after {
      opacity: 0.3;
    }
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.main-nav__toggle span,
.main-nav__toggle::before,
.main-nav__toggle::after {
  position: absolute;
  left: 0;
  height: 3px;
  width: 100%;
  transition: all .3s ease 0s;
  background-color: $color-white;
}

.main-nav__toggle span {
  top: 50%;
  transform: scale(1) translate(0px, -50%);
}

.main-nav__toggle::before,
.main-nav__toggle::after {
  content: "";
}

.main-nav__toggle::before {
  top: 0;
}

.main-nav__toggle::after {
  bottom: 0;
}

.main-nav--opened .main-nav__toggle span {
  transform: scale(0) translate(0px, -50%);
}

.main-nav--opened .main-nav__toggle::before {
  top: 50%;
  transform: rotate(-45deg) translate(0px, -50%);
}

.main-nav--opened .main-nav__toggle::after {
  bottom: 50%;
  transform: rotate(45deg) translate(0px, 50%);
}