.form-report {
  text-align: left;

  @media(max-width: $mobile-width-only) {
    margin: 0 -20px 50px;

    padding: 50px 0;
    background-color: #F2F2F2;
  }
}

.form__inner {
  padding: 0 20px;

  @media(min-width: $tablet-width) {
    padding: 0;
  }
}

.form-legend--report {

  @media(max-width: $mobile-width-only) {
    width: 100%;
    padding: 40px 20px 0;
    background-color: #F2F2F2;
  }
}

// Form-report--achievements

.form-report--achievements {

  @media(min-width: $tablet-width) {
    order: -1;
  }

  @media(min-width: $desktop-width) {
    width: 100%;
  }
}

.form-report--achievements .form__inner {

  @media(min-width: $desktop-width) {
    column-count: 3;
  }
}

.form-checkbox {

  @media(min-width: $desktop-width) {

    &:nth-of-type(2n+2) {
      margin-bottom: 0;
    }
  }
}

// Form-report--applications

.form-group.form-report--applications {

  @media(min-width: $desktop-width) {
    padding: 25px 40px;
  }
}

.form-report--applications {

  @media(min-width: $tablet-width) {
    order: -3;
  }
}