.logo__icon {
  fill: $color-white;
  transition: .5s fill;

  &:hover {
    fill: $color-pink;
    transition: .5s fill;
  }

  &:active {
    fill: $color-white;
    opacity: .3;
    transition: .2s opacity;
  }
}

.logo__footer {
  fill: $color-dark-grey;
  transition: .5s fill;

  &:active {
    fill: $color-dark-grey;
    opacity: .3;
    transition: .2s opacity;
  }
}

.logo__icon--mobile {
  @media (min-width: $tablet-width) {
    display: none;
  }
}

.logo__icon--tablet {
  display: none;

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    display: block;
  }
}

.logo__icon--desktop {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
  }
}

.logo__icon-footer--tablet {
  @media (min-width: $desktop-width) {
    display: none;
  }
}

.logo__icon-footer--desktop {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
  }
}