.panorama {
  min-width: 100%;
  margin-bottom: 20px;
}

.panorama__picture {
  min-width: 100%;
}

.panorama__wrapper {
  position: relative;
}

.panorama__identifier {
  position: absolute;
  right: 5%;
  padding: 6px 20px;

  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  line-height: 24px;

  color: $color-white;
  background-color: $color-pink;

  @media (min-width: $tablet-width) {
    top: 15%;
  }

  @media (min-width: $desktop-width) {
    right: 15%;
  }
}

.panorama__info {
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  min-height: auto;
}

.panorama__author {
  margin: 0;
}

.panorama__desc {
  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    order: 2;
  }
}

.panorama__likes {

  @media (min-width: $tablet-width) {
    margin-top: 0;
  }

  &::before {
    margin-left: 20px;
  }
}