.site-list__item {
  box-shadow: 0px -1px 0px $color-black,
    inset 0px 1px 0px rgba($color-white, 0.15);

  @media (min-width: $desktop-width) {
    padding: 0;
    box-shadow: none;
    margin-right: 80px;
  }

  &:last-child {
    border-bottom: 1px solid $color-black;

    @media (min-width: $desktop-width) {
      border-bottom: none;
      margin-right: 0;
    }
  }
}

.site-list__link {
  display: block;
  text-transform: uppercase;

  font-size: 18px;
  font-weight: 700;
  line-height: 32px;

  padding: 25px 0;

  color: $color-white;
  transition: .5s color;

  @media (min-width: $desktop-width) {
    text-transform: none;

    font-weight: 400;
  }

  &:hover {
    color: $color-pink;
    transition: .5s color;
  }

  &:active {
    opacity: .3;
    transition: .2s opacity;

    @media (min-width: $desktop-width) {
      color: $color-white;
      opacity: .3;
    }
  }
}

.site-list__item--active .site-list__link {
  @media (min-width: $desktop-width) {
    padding-bottom: 10px;
    border-bottom: 2px solid $color-white;
  }
}

.site-list__item.site-list__item--active a {
  pointer-events: none;

  &:hover {
    color: $color-white;
  }

  &:active {
    opacity: 1;
  }
}