.form-textarea__input {
  font-size: 18px;
  line-height: 30px;

  width: 100%;
  min-height: 200px;

  padding: 20px;

  border: 2px solid $color-grey-white;
  border-radius: 1px;

  outline: none;

  &:hover {
    border: 2px solid $color-dark-grey;
    border-radius: 1px;
  }

  &:focus {
    border: 2px solid $color-pink;
    border-radius: 1px;
  }
}