.social__list {
  @include reset-list;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social__item {
  margin-right: 10px;

  &:last-child {
    margin-right: 0;
  }
}

.social__link {
  position: relative;
  display: block;
  width: 50px;
  height: 50px;

  border: 3px solid rgba($color-dark-grey, .1);
  border-radius: 50px;
  transition: .2s border;

  &:hover {
    border: 3px solid rgba($color-dark-grey, 1);
    transition: .5s border;
  }

  &:active {
    border: 3px solid rgba($color-dark-grey, .1);

    & svg {
      opacity: .3;
    }
  }
}

.social__link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
}