.form-checkbox__input+label::before {
  content: "";
  position: absolute;
  left: 0;
  width: 35px;
  height: 35px;

  border: 2px solid $color-grey-white;
  border-radius: 1px;
}

.form-checkbox__input:focus+label::before {
  border: 2px solid $color-dark-grey;
}

.form-checkbox__input:checked+label::after {
  content: "";
  position: absolute;
}

.form-checkbox__input:checked+label::after {
  top: 40%;
  left: 7px;
  width: 21px;
  height: 8px;
  border-left: 2px solid $color-pink;
  border-bottom: 2px solid $color-pink;

  transform: rotate(-45deg);
}

.form-checkbox__label {
  display: block;
  cursor: pointer;
  user-select: none;

  font-size: 18px;
  line-height: 30px;

  position: relative;
  padding-left: 50px;

  &:hover::before {
    border: 2px solid $color-dark-grey;
  }

  &:active::before {
    border: 2px solid $color-pink;
  }
}