.feedback__banner {
  line-height: 30px;
  margin: 0;
  margin-bottom: 50px;

  br {
    display: none;

    @media(min-width: $tablet-width) and (max-width: $desktop-width) {
      display: block;
    }
  }

  & span {

    @media(max-width: $mobile-width-only) {
      display: none;
    }
  }

  @media(min-width: $tablet-width) {
    font-size: 18px;
  }
}

.feedback__form {
  display: flex;
  flex-direction: column;

  @media(min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}