*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
}

body {
  width: 100%;
  min-width: 300px;
  height: 100%;

  font-style: normal;
  font-weight: 400;
  font-family: $font-family--open-sans;

  color: $color-dark-grey;
}

main {
  padding-top: 100px;
}

a {
  text-decoration: none;
}

h1,
h2 {
  font-size: 24px;
  font-weight: 300;
  line-height: 120%;

  @media (min-width: $tablet-width) {
    font-size: 40px;
  }

  @media (min-width:$desktop-width) {
    font-size: 50px;
  }
}

h3 {
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;

  @media (min-width:$desktop-width) {
    font-size: 30px;
    line-height: 30px;
  }
}

img,
video {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

textarea {
  resize: none;
}

// firefox placeholder \ invalid fix + ios bdrs
input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}

// chrome search X removal
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}