.reviews {
  padding-top: 50px;
}

.reviews__wrapper {
  position: relative;
}

.reviews__image {
  display: none;

  @media(min-width: $desktop-width) {
    display: block;
    margin: 0 auto;
  }
}

.reviews__content-wrapper {
  display: flex;
  margin-bottom: 40px;
}

.reviews__item {
  margin: 0;
}

.reviews__author-info {
  display: flex;
  flex-direction: column;

  @media(min-width: $desktop-width) {
    padding: 50px 100px;
  }
}

.reviews__author-name {
  font-size: 18px;
  font-weight: bold;
  font-style: normal;
  line-height: 30px;
  margin-bottom: 50px;

  @media(min-width: $tablet-width) {
    font-size: 20px;
    order: 1;
    margin: 0;
  }

  @media(min-width: $desktop-width) {
    font-size: 24px;
  }
}

.reviews__author-name span {
  display: none;

  @media(min-width: $tablet-width) {
    display: block;
    font-size: 14px;
    font-weight: normal;
  }

  @media(min-width: $desktop-width) {
    font-size: 18px;
  }
}

.reviews__text {
  font-size: 14px;
  line-height: 30px;
  margin: 0;

  @media(min-width: $tablet-width) {
    font-size: 18px;
    margin-bottom: 50px;
  }
}

.reviews__pagination {
  display: flex;
  justify-content: center;
  align-items: center;

  @media(min-width: $desktop-width) {
    display: none;
  }
}