.page-footer__inner {
  padding: 40px 0;

  @media(min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.page-footer__logo {
  display: block;

  @media(max-width: $mobile-width-only) {
    margin-bottom: 40px;
  }
}

.page-footer__social {
  @media(max-width: $mobile-width-only) {
    margin-bottom: 40px;
  }
}

.copyright__link {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $color-dark-grey;
  transition: .5s color, .5s font-weight;

  &:hover {
    font-weight: bold;
    transition: .5s font-weight;

    svg {
      fill: currentColor;
      transition: .5s fill;
    }
  }

  &:active {
    opacity: .3;
    transition: .2s opacity;
  }
}

.copyright__link svg {
  margin-left: 10px;
  fill: $color-pink;
  transition: .5s fill;
}