.advantages__slogan {
  margin: 0;
  margin-bottom: 25px;

  @media(max-width: $mobile-width-only) {
    br:last-child {
      display: none;
    }
  }

  @media(min-width: $tablet-width) {
    line-height: 54px;
    margin-bottom: 35px;

    br {
      display: block;
    }
  }

  @media(min-width: $desktop-width) {
    margin-bottom: 70px;
  }
}

.advantages__inner {
  padding-top: 90px;

  background-color: #F2F2F2;
  background-image: url("../img/index/triangle.svg");
  background-repeat: no-repeat;
  background-position: 100% 0%;
  background-size: cover;

  @media(min-width:$tablet-width) {
    padding-top: 0;
  }
}

.advantages__inner .container {
  @media(min-width: $tablet-width) {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
  }

  @media(min-width: $desktop-width) {
    flex-direction: column-reverse;
    align-items: center;
  }
}

.advantages__list {
  @include reset-list;
  text-align: center;
  margin-bottom: 48px;

  @media(min-width: $tablet-width) {
    text-align: left;
    margin-bottom: 0;

    br {
      display: none;
    }
  }

  @media(min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 55px;
    text-align: center;
  }
}

.advantages__item {
  margin-bottom: 35px;

  &:last-child {
    margin-bottom: 45px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;

    &:last-child {
      margin: 0;
    }
  }
}

.advantages__title {
  margin: 0;
  margin-bottom: 12px;

  @media (min-width: $tablet-width) {
    margin-bottom: 12px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 30px;
  }
}

.advantages__desc {
  line-height: 24px;
  margin: 0;

  @media(min-width: $desktop-width) {
    font-size: 18px;
    line-height: 30px;
  }
}

.advantages__photos {
  max-height: 277px;
  overflow: hidden;

  @media(min-width: $tablet-width) {
    min-height: 482px;
    min-width: 268px;
    margin-right: 42px;
  }

  @media(min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 35px;
    min-height: 300px;
    margin-bottom: 55px;
    margin-right: 0;
  }
}

.advantages__image {
  width: 157px;
  height: 320px;

  margin: 0 auto;

  @media(min-width: $tablet-width) {
    min-width: 268px;
    height: 547px;
  }

  @media(min-width: $desktop-width) {
    width: auto;
    height: 382px;
  }
}

.advantages__image:nth-child(-n+2) {
  display: none;

  @media(min-width: $desktop-width) {
    display: block;
  }
}