.photo__banner {
  line-height: 30px;
  margin: 0;
  margin-bottom: 50px;

  @media (min-width: $tablet-width) {
    font-size: 18px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 100px;
  }
}

.photo__list {

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 50px;
  }
}

.photo__item {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    width: 300px;
    margin: 0;
  }
}

.photo__picture {
  min-width: 100%;
}

.photo__info {
  margin: 0;
  padding: 25px 20px;
  text-align: left;
  background-color: #F2F2F2;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    min-height: 170px;
  }
}

.photo__author {
  line-height: 24px;
  margin: 0;
}

.photo__date {
  font-weight: 500;
  font-size: 12px;
  text-transform: uppercase;
}

.photo__desc {
  margin: 0;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 24px;
}

.photo__likes {
  display: flex;
  align-items: baseline;
  margin: 0;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 24px;

  color: $color-pink;

  &::before {
    content: "";
    width: 13px;
    height: 12px;
    margin-right: 20px;
    background-image: url("../img/photo/heart.svg");
    cursor: pointer;

    @media (min-width: $tablet-width) {
      margin-right: 0;
    }
  }

  @media (min-width: $tablet-width) {
    margin-top: auto;
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}