.form-group {
  margin: 0;
  padding: 0;
  margin-bottom: 50px;
  border: 2px solid $color-grey-white;
  border-radius: 1px;

  @media(max-width: $mobile-width-only) {
    border: none;
  }

  &:not(:first-child) {
    @media(min-width: $tablet-width) {
      padding: 40px 60px;
    }
  }
}

.form-legend {
  text-transform: uppercase;
  line-height: 18px;
  padding: 0 20px;
  color: $color-pink;
  text-align: center;

  @media(min-width: $tablet-width) {
    white-space: nowrap;
  }
}

.form__item {
  margin-bottom: 20px;

  @media(min-width: $tablet-width) {
    margin-bottom: 40px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

// Form-post

.form__post {
  width: 100%;

  @media(min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.form__button {

  @media(max-width: $mobile-width-only) {
    margin-bottom: 40px;
  }

  color: $color-white;
  line-height: 30px;
  text-transform: uppercase;

  background: $color-light-green;
  border: 2px solid $color-light-green;
  border-radius: 1px;

  &:hover {
    background: $color-green;
    border: 2px solid $color-green;
    transition: border .2s, background .2s;
  }

  &:active {
    &:active {
      color: rgba($color-white, 0.3);
    }
  }
}

.form__required {
  text-transform: uppercase;
  margin: 0;

  sup {
    color: $color-pink;
  }

  span {
    display: none;
  }

  @media(min-width: $desktop-width) {

    span {
      display: inline;
    }
  }
}