.form-text__label {
  display: block;
  cursor: pointer;
  user-select: none;

  text-transform: uppercase;
  text-align: left;
  line-height: 30px;

  padding: 10px 20px;

  color: $color-pink;
}

.form-text__input {
  font-size: 18px;
  line-height: 30px;

  width: 100%;
  padding: 15px 20px;

  border: 2px solid $color-grey-white;
  border-radius: 1px;

  outline: none;

  &:hover {
    border: 2px solid $color-dark-grey;
    border-radius: 1px;
  }

  &:focus {
    border: 2px solid $color-pink;
    border-radius: 1px;
  }
}

// Form-input--error 

.form-input--error {
  border: 2px solid $color-red;
}