.form-radio__input+label::before {
  content: "";
  position: absolute;

  left: 0;
  width: 37px;
  height: 37px;

  border: 2px solid $color-grey-white;
  border-radius: 50%;
}

.form-radio__input:focus+label::before {
  border: 2px solid $color-dark-grey;
}


.form-radio__input:checked+label::after {
  content: "";
  position: absolute;
}

.form-radio__input:checked+label::after {
  top: 35%;
  left: 10px;
  width: 16px;
  height: 16px;
  background-color: $color-pink;
  border-radius: 50%;
}

.form-radio__label {
  display: block;
  cursor: pointer;
  user-select: none;

  font-size: 18px;
  line-height: 30px;

  position: relative;
  padding-left: 50px;

  &:hover::before {
    border: 2px solid $color-dark-grey;
  }

  &:active::before {
    border: 2px solid $color-pink;
  }
}