.form-group.form-contacts {

  @media(min-width: $tablet-width) {
    padding-bottom: 0;
  }
}

.form-contacts {

  @media(min-width: $tablet-width) {
    order: -2;
  }

  @media(min-width: $desktop-width) {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}

.form-legend--contacts {

  @media(max-width: $mobile-width-only) {
    display: none;
  }
}

.form-contacts .form__item {

  @media(min-width: $tablet-width) {
    display: flex;
    flex-direction: column-reverse;
    margin: 0;
  }

  @media(min-width: $desktop-width) {
    flex-basis: 45%;
  }
}

.form-contacts .form-text__input {
  padding-right: 80px;
}

.form__item--phone,
.form__item--email {

  @media (min-width: $tablet-width) {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      right: 2px;
      top: 2px;
      width: 60px;
      height: 60px;
    }
  }
}

.form__item--phone {
  &::after {
    background: $color-grey-white url(../img/form/phone-icon.svg) no-repeat 50% 50%;
  }
}

.form__item--email {
  &::after {
    background: $color-grey-white url(../img/form/mail-icon.svg) no-repeat 50% 50%;
  }
}