.research {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    padding-top: 55px;
    padding-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    background-color: #F2F2F2;
  }
}

.research .container {
  display: flex;
  align-items: center;

  &::before {
    content: "";
    flex-basis: 50%;
    width: 238px;
    height: 276px;

    background-image: url(../../../img/index/research-timer.svg);
    background-repeat: no-repeat;

    @media (min-width: $desktop-width) {
      flex-basis: 40%;
      width: 298px;
      height: 345px;
      background-position: left;
      background-size: contain;
    }
  }
}

.research__desc {
  flex-basis: 50%;
  text-align: left;

  @media (min-width: $desktop-width) {
    flex-basis: 60%;
  }
}

.research__title,
.research__text {
  margin: 0;
  margin-bottom: 25px;
}

.research__title {
  font-size: 30px;
  line-height: 36px;

  @media (min-width: $desktop-width) {
    line-height: 30px;
  }
}

.research__text {
  line-height: 24px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 30px;
  }
}

.research__link {
  line-height: 24px;
  text-decoration: underline;

  color: $color-pink;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 30px;
  }
}