.intro__inner {
  padding-top: 100px;
  padding-bottom: 30px;

  @media (min-width: $tablet-width) {
    padding-top: 200px;
    padding-bottom: 100px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 130px;
  }
}

.intro__inner--index {
  position: relative;

  @media (min-width: $tablet-width) {
    width: 300px;
    z-index: 0;
  }
}

.intro__picture {
  margin: auto;
  min-width: 320px;

  @media (min-width: $tablet-width) {
    position: absolute;

    height: 542px;
    min-width: 660px;
    top: auto;
    bottom: 0;
    z-index: -1;
  }

  @media (min-width: $desktop-width) {
    height: 645px;
    min-width: 1050px;
  }
}

.intro__title,
.intro__text {
  margin: 0;
  line-height: 33px;
  color: $color-white;

  @media (min-width: $tablet-width) {
    font-size: 50px;
    line-height: 68px;
  }
}

.intro__title--index {
  line-height: 120%;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    font-size: 40px;
    margin-bottom: 39px;
  }

  @media (min-width: $desktop-width) {
    font-size: 50px;
  }
}

.intro__title--form {

  & br:first-child {

    @media (min-width: $desktop-width) {
      display: none;
    }
  }
}

.intro__text {
  line-height: 24px;

  @media (min-width: $tablet-width) {
    font-size: 18px;
  }
}

.intro__button {
  margin: 15px 0;
  border: 3px solid $color-white;
  border-radius: 30px;
  color: $color-white;

  @media (min-width: $tablet-width) {

    margin-bottom: 67px;
  }

  @media (min-width: $desktop-width) {
    font-size: 21px;
    padding: 20px 30px;
  }

  &:hover {
    color: $color-pink;
    background-color: $color-white;
    transition: .2s color, .2s background-color;
  }

  &:active {
    color: rgba($color-pink, 0.3);
  }
}

.intro__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;

  margin-bottom: 32px;

  @media (min-width: $desktop-width) {

    margin-bottom: 58px;
  }
}

.intro__item {
  margin-right: 35px;

  &:nth-child(3n) {
    margin-right: 0;
  }
}