.filter__list {
  @include reset-list;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 75px;
  margin-bottom: 30px;
  border-bottom: 1px solid rgba(40, 54, 69, 0.1);

  @media (min-width:$tablet-width) {
    flex-direction: column;
    border: none;
    padding-bottom: 0;
  }
}

.filter__item {

  @media (min-width:$tablet-width) {
    display: flex;

    border-bottom: 1px solid rgba(40, 54, 69, 0.1);
    padding: 20px 0;
  }
}

.filter__item:not(:last-child) {
  margin-right: 40px;

  @media (min-width:$tablet-width) {
    margin-right: 0;

  }
}

.filter__tab {
  @include reset-btn;
  fill: $color-dark-grey;
  opacity: 0.3;
  cursor: pointer;

  &:hover {
    @media (max-width: $mobile-width-only) {
      fill: $color-dark-grey;
      opacity: 0.5;
    }
  }

  @media (min-width:$tablet-width) {
    fill: $color-pink;
    opacity: 1;
    cursor: initial;
  }
}

.filter__tab--active {
  fill: $color-pink;
  opacity: 1;
}

.filter__img {

  @media (min-width:$tablet-width) {
    margin-right: 40px;
    width: 24px;
  }

  @media (min-width:$desktop-width) {
    width: 48px;
  }

  &--crop {
    @media (min-width:$tablet-width) {
      height: 22px;
    }

    @media (min-width:$desktop-width) {
      height: 44px;
    }
  }

  &--fill {
    @media (min-width:$tablet-width) {
      height: 22px;
    }

    @media (min-width:$desktop-width) {
      height: 43px;
    }
  }

  &--contrast {
    @media (min-width:$tablet-width) {
      height: 16px;
    }

    @media (min-width:$desktop-width) {
      height: 32px;
    }
  }
}

.filter__content {
  visibility: hidden;
  position: absolute;
  top: 70%;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 240px;
  height: 2px;
  background-color: $color-dark-grey;

  @media (min-width:$tablet-width) {
    visibility: visible;
    position: relative;
    width: 200px;
  }

  @media (min-width:$desktop-width) {
    width: 282px;
  }
}

.filter__content--active {
  visibility: visible;
}

.filter__interactive {
  position: absolute;
  top: -6px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: $color-white;
  border: 2px solid $color-pink;
  cursor: pointer;

  &--crop {
    left: 80%;
  }

  &--fill {
    left: 10%;
  }

  &--contrast {
    left: 50%;
  }

  &:hover,
  &:active {
    background: $color-pink;
  }
}

.filter__buttons-wrapper {
  width: 300px;
  margin: 0 auto;

  @media (min-width:$desktop-width) {
    width: auto;
  }
}

.filter__button {
  min-width: 280px;
  padding: 15px 90px;
  color: $color-pink;
  border: 3px solid $color-pink;
  border-radius: 30px;

  @media (min-width:$tablet-width) {
    min-width: 260px;
    padding: 15px 75px;
  }

  @media (min-width:$tablet-width) {
    min-width: 183px;
    padding: 15px 40px;
  }

  &:hover {
    color: #FFFFFF;
    background-color: $color-pink;
  }

  &:active {
    opacity: 0.3;
  }
}

.filter__button--add {
  margin-bottom: 20px;

  @media (min-width:$desktop-width) {
    margin-bottom: 0;
  }
}

.filter__button--cansel {
  color: $color-dark-grey;
  opacity: 0.1;
  border: 3px solid $color-dark-grey;
  cursor: initial;

  &:hover {
    opacity: 0.3;
    color: initial;
    background-color: initial;
  }

  &:active {
    background-color: $color-dark-grey;
  }
}