.contacts {
  padding-top: 50px;
}

.contacts__title {
  font-size: 24px;

  margin: 0;
  margin-bottom: 45px;

  @media(min-width: $tablet-width) {
    font-size: 40px;
    line-height: 54px;
  }

  @media(min-width: $desktop-width) {
    font-size: 50px;
    line-height: 68px;
  }
}

.contacts__info {
  @media(min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.contacts__info p {
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;

  margin: 0;
  margin-bottom: 30px;

  &:first-child {
    order: -2;
  }

  &:last-child {
    @media(min-width: $tablet-width) and (max-width: $desktop-width) {
      flex: 0 0 100%;
    }

    @media(min-width: $desktop-width) {
      order: -1;
    }
  }
}

.contacts__info a {
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;

  color: $color-pink;


}

.contacts__email {
  &:hover {
    color: $color-dark-grey;
    text-decoration: underline;
  }

  &:active {
    text-decoration: none;
    opacity: 0.3;
  }
}

.contacts__map {
  height: 200px;

  @media(min-width: $tablet-width) {
    height: 374px;
  }

  @media(min-width: $desktop-width) {
    height: 502px;
  }
}