.tariff {
  padding-top: 90px;
  padding-bottom: 50px;

  background-color: #f2f2f2;
  background-image: url(../img/index/triangle.svg);
  background-repeat: no-repeat;
  background-position: 100% 0;
  background-size: cover;
}

.tariff .container {
  @media(max-width:$mobile-width-only) {
    padding: 0;
  }
}

.tariff__wrapper {
  @media(max-width:$mobile-width-only) {
    overflow: hidden;
    padding: 0 20px;
  }
}

.radio__btn--1:checked~.tariff__toggles .tariff__toggle--1,
.radio__btn--2:checked~.tariff__toggles .tariff__toggle--2,
.radio__btn--3:checked~.tariff__toggles .tariff__toggle--3 {
  background-color: $color-white;
  border: 3px solid $color-dark-grey;
  opacity: 1;
  box-sizing: content-box;
}

#tariff-1:checked~.tariff__table {
  @media(max-width:$mobile-width-only) {
    transform: translateX(0%);
  }
}

#tariff-2:checked~.tariff__table {
  @media(max-width:$mobile-width-only) {
    transform: translateX(-33.33%);
  }
}

#tariff-3:checked~.tariff__table {
  @media(max-width:$mobile-width-only) {
    transform: translateX(-66.66%);
  }
}

.tariff__toggles {
  padding-top: 45px;
}

.tariff__toggle {
  margin-right: 15px;

  &:last-child {
    margin-right: 0;
  }
}

.tariff__toggles label {
  content: '';
  display: inline-block;
  width: 8px;
  height: 8px;
  vertical-align: middle;
  background-color: rgba(40, 54, 69, .3);
  border-radius: 50%;
  cursor: pointer;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.1;
  }

  @media(min-width:$tablet-width) {
    display: none;
  }
}

.tariff__table {
  @media(max-width:$mobile-width-only) {
    width: 300%;
    transition: transform .3s;
  }

  text-transform: uppercase;
  width: 100%;
  border-collapse: collapse;
}

.tariff__table p {
  margin: 0;
}

.tariff__table th {
  background: $color-dark-grey;
  border: 2px solid $color-dark-grey;
  border-right: 2px solid #f2f2f2;
  border-top: 0;
  padding: 20px 20px;

  &:first-child {
    border: none;
  }

  &:nth-child(2) {
    border-left: 0;
    border-radius: 5px 0px 0px 0px;
  }

  &:last-child {
    border-right: 0;
    border-radius: 0px 5px 0px 0px;
  }
}

.tariff__header {
  width: 25%;
  color: $color-white;

  &:first-child {
    display: none;

    @media (min-width: $tablet-width) {
      display: table-cell;
      background: transparent;
    }
  }
}

.tariff__title {
  font-weight: bold;
  font-size: 16px;
  line-height: 30px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
  }
}

.tariff__price {
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;

  @media (min-width: $desktop-width) {
    font-size: 24px;
  }
}

.tariff__table td {
  padding: 20px 20px;

  background-color: $color-white;
  border: 3px solid $color-grey-white;
}

.tariff__option {

  &:first-child {
    display: none;
    line-height: 30px;

    @media (min-width: $tablet-width) {
      display: table-cell;
      text-transform: initial;
    }

    @media (min-width: $desktop-width) {
      font-size: 18px;
    }
  }
}

.tariff__inner .tariff__name {
  white-space: nowrap;
  line-height: 30px;
  margin-right: 30px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.tariff__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $tablet-width) {
    justify-content: center;
  }
}

.tariff__choose--no {
  width: 27px;
  height: 27px;

  background: url(../img/index/tariff-no.svg);
}

.tariff__choose--yes {
  width: 31px;
  height: 21px;

  background: url(../img/index/tariff-yes.svg);
}