.modal {
  display: none;
  position: fixed;

  text-align: center;
  width: 100%;
  top: 20%;

  margin: auto;
  padding: 50px 40px;

  box-sizing: border-box;
  background-color: $color-white;
  box-shadow: 0 7px 15px 5px rgba($color-black, .2);
  border-radius: 5px;

  z-index: 100;

  @media (min-width: $tablet-width) {
    width: 80%;
    left: 10%;
    padding: 50px 120px;
  }

  @media (min-width: $desktop-width) {
    width: 60%;
    left: 20%;
    padding: 50px 200px;
  }
}

.modal--show {
  display: block;
  animation-name: bounce;
  animation-duration: 0.6s;
}

.modal__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  margin: 0 0 40px;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 40px;
  }
}

.modal__desc {
  line-height: 24px;
  margin: 0 0 50px;

  @media (min-width: $tablet-width) {
    font-size: 18px;
  }
}

.modal__button {
  width: 100%;
  margin: 0;
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }

  70% {
    transform: translateY(30px);
  }

  90% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}