.upload {

  background-color: #F2F2F2;

  @media (min-width:$desktop-width) {
    padding: 60px 0;
    background-color: transparent;
  }
}

.upload__banner {
  text-align: center;
  font-size: 14px;
  line-height: 30px;
  margin: 0;
  padding-bottom: 50px;
  background-color: $color-white;

  @media (min-width:$tablet-width) {
    font-size: 18px;
    padding-bottom: 70px;
  }

  @media (min-width:$desktop-width) {
    padding-bottom: 100px;
  }
}

.upload__wrapper {
  padding: 30px 0;

  @media (min-width:$tablet-width) {
    display: flex;
    justify-content: space-between;
  }

  @media (min-width:$desktop-width) {
    padding: 60px 100px;
    background-color: #F2F2F2;
  }
}

.upload__picture {
  margin: 0 auto;
  margin-bottom: 40px;

  @media (min-width:$tablet-width) {
    width: 300px;
    height: 400px;
    margin-bottom: 0;
  }
}

.upload__filter {
  @media (min-width:$tablet-width) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}